import React from 'react'
import LoadingTable from './LoadingTable'
import './table.css'
const Table = ({ users }) => {
	return (
		<>
			 
						<table className="table   table-striped  table-bordered table-hover"  width="100%" cellSpacing="0">
							<thead>
								<tr>
									<th>Nombre</th>
									<th>Apellido</th>
									<th>Email</th>

									<th>Avatar</th>
								</tr>
							</thead>

							<tbody>
								{!users.length ? (
									 
										<LoadingTable /> 
									 
								) : (
									users.map((user) => (
										<tr key={`${user.name}${user.lastname}`}>
											<td>{user.name}</td>
											<td>{user.lastname}</td>
											<td>{user.email}</td>
											<td>
												<img
													className="table__avatar"
													src={`https://azvi.herokuapp.com/images/users/${user.image}`}
													alt=""
												/>
											</td>
										</tr>
									))
								)}
							</tbody>
						</table>
				 
		</>
	)
}

export default Table
